/*index.css*/
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p,
h1 {
  text-align: center;
}

/*ul,li.css*/
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

ul li {
  padding: 5px 0;
}

/*Button.css*/
.button {
  margin: 10px 0px;
  padding: 3px 15px;
  border-radius: 5px;
}

.button:hover {
  background-color: #cdcdcd;
}

/* Loader.css */
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader p {
  padding: 0 20px;
}

/* Navbar.css */
nav {
  border: 1px solid #f0f0f0;
  border-radius: 5px;

  margin: 15px;
  padding: 15px;
}

nav a {
  text-decoration: none;
  font-weight: bold;
  padding: 0.5rem 1rem;
  color: black;
  transition: background-color 0.3s, color 0.3s;
}

nav a:hover {
  border: 1px solid #f0f0f0;
  background-color: #cdcdcd;
}

/*Wrapper.css*/
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/*Home.css*/
.home-container p {
  max-width: 50ch;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
}
